body{
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  /* background:linear-gradient(rgba(0, 0, 0, 0.5),rgba(0, 0, 0, 0.5)), url(bg.jpg) no-repeat; */
  background-size: cover;
}
.login-box{
  width: 280px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  color: white;
}
.login-box h1{
  float: left;
  font-size: 40px;
  border-bottom: 6px solid #4caf50;
  margin-bottom: 50px;
  padding: 13px 0;
}
.textbox{
  width: 100%;
  overflow: hidden;
  font-size: 20px;
  padding: 8px 0;
  margin: 8px 0;
  border-bottom: 1px solid #4caf50;
}
.textbox i{
  width: 26px;
  float: left;
  text-align: center;
}
.textbox input{
  border: none;
  outline: none;
  background: none;
  color: white;
  font-size: 18px;
  width: 80%;
  float: left;
  margin: 0 10px;
}
.btnn{
  width: 100%;
  background: none;
  border: 2px solid #4caf50;
  color: white;
  padding: 5px;
  font-size: 18px;
  cursor: pointer;
  margin: 12px 0;
}
.admin{

    position: absolute;
    top: 10%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    color: white;
}
.wrong{

    position: absolute;
    top: 90%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    color: white;
}
.footer{
    background-color: #1a1a00;
    color: white;
}
.foot{
    background-color: midnightblue;
    color: mistyrose;
}
.clock{
    text-align: left !important;
    color: white;
    margin: auto;
    font-family: 'Times New Roman', Times, serif;
}
.date{
    text-align: inherit;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.fa-facebook-square{
    font-size: 20px !important;
    color: white !important;
}
.fa-twitter-square{
    font-size: 20px !important;
    color: white !important;
}
.fontsize{
    font-size: 30px;
}
